     <el-form label-width=\"80px\">\n          <el-form-item label=\"产品名称\">\n            <el-input placeholder=\"请输入产品名称\"\n                      v-model=\"product\"></el-input>\n          </el-form-item>\n          <el-form-item label=\"产品价值\">\n            <el-input placeholder=\"请输入产品价值\"\n                      v-model=\"price\"\n                      class=\"Product_value\"></el-input>\n          </el-form-item>\n          <el-form-item label=\"中奖概率\">\n            <el-input placeholder=\"请输入中奖概率\"\n                      v-model=\"prob\"\n                      class=\"probability\"></el-input>\n          </el-form-item>\n          <el-form-item label=\"领取方式\">\n            <el-radio label=\"1\"\n                      v-model=\"redio\">到店领取</el-radio>\n            <el-radio label=\"0\"\n                      v-model=\"redio\">邮寄到家</el-radio>\n          </el-form-item>\n          <el-form-item label=\"产品图片\">\n            <!--\t<img :src=\"dialogImageUrl\" v-show=\"defaultImg\" class=\"defaultImg\" @click=\"modifyUp\" />-->\n            <el-upload action=\"https://ceshi.ctbls.com/Gift/file\"\n                       :file-list=\"fileList2\"\n                       :limit=\"limit\"\n                       :on-success=\"success\"\n                       :data=\"obj\"\n                       name=\"image\"\n                       list-type=\"picture-card\"\n                       :on-preview=\"handlePictureCardPreview\">\n              <i class=\"el-icon-plus\"></i>\n            </el-upload>\n            <el-dialog :visible.sync=\"dialogVisible\">\n              <img width=\"100%\"\n                   :src=\"dialogImageUrl\"\n                   alt=\"\">\n            </el-dialog>\n          </el-form-item>\n\n          <el-form-item label=\"产品描述\">\n            <div class=\"editor-container\">\n              <!-- <UE class='ue'></UE> -->\n\n              <wangEditor class=\"ue\"\n                          :catchData=\"catchData\"\n                          @img=\"img\"\n                          :content=\"str\"></wangEditor>\n            </div>\n          </el-form-item>\n          <div style=\"width: 100%; text-align: center;\">\n            <el-button type=\"primary\"\n                       @click=\"upload\">{{index?\"修改\":\"上传\"}}</el-button>\n          </div>\n\n        </el-form>\n      </div>\n\n    </div>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport page from '../common.js';\nimport wangEditor from '../common/wangEditor.vue';\nexport default {\n  data () {\n    return {\n      list: [],\n      product: \"\", //产品名称\n      price: \"\", //产品价值\n      prob: \"\", //产品概率\n      redio: \"0\", //领取方式\n      dialogImageUrl: '', //产品图片\n      content: \"\", //产品描述\n      dialogVisible: false,\n      Btnupload: \"上传\",\n      token: window.sessionStorage.getItem('bbytoken'),\n      //分页数据\n      authList: [],\n      curId: 0,\n      seCurId: 0,\n      imgUrl: \"\",\n      index: \"\",\n      url: \"Gift/add\",\n      up: true,\n      defaultImg: false,\n      limit: 1,\n      obj: {\n        token: window.sessionStorage.getItem('bbytoken')\n      },\n      str: \"\",\n      fileList2: [],\n    }\n  },\n  components: {\n    wangEditor\n  },\n  created () {\n\n  },\n  methods: {\n    img (url) {\n\n    },\n    catchData (value) {\n\n      this.content = value\n\n    },\n    change () {\n\n    },\n    handlePictureCardPreview (file) {\n      this.dialogImageUrl = file.url;\n      this.dialogVisible = true;\n\n    },\n    success (res, file) {\n      this.imgUrl = res;\n\n    },\n    upload () {\n      var that = this\n\n      var obj = {\n        token: this.token,\n        name: this.product,\n        price: this.price,\n        prob: this.prob,\n        draw: this.redio,\n        image: this.imgUrl,\n        content: this.content.replace(/<[^>]+>/g, \"\"),\n        id: this.index\n      }\n      this.$axios.post(this.url, obj).then(res => { //点击上传请求的接口\n        if (res.data.code == 0) {\n          layer.msg(res.data.msg)\n        } else {\n\n          if (res.data.msg == \"上传成功\") {\n            layer.msg(res.data.msg, {\n              time: 3000\n            }, function () {\n              that.$router.go(0)\n            })\n          } else {\n            layer.msg(res.data.msg, {\n              time: 3000\n            }, function () {\n              that.$router.go(\"-1\")\n            })\n          }\n\n        }\n\n      }).catch((err) => {\n\n      })\n    },\n\n\n  },\n  mounted () {\n\n    var id = this.$route.query.id\n    this.index = this.$route.query.index\n    if (this.index) {\n\n      this.up = false\n      this.defaultImg = true\n      this.url = \"Gift/update\"\n      this.$axios.post(\"Gift/Check\", { //点击修改请求的接口\n        token: this.token,\n        id: this.index\n      }).then(res => {\n        var data = res.data.data;\n        this.product = data.name\n        this.price = data.price\n        this.prob = data.prob\n        this.redio = data.draw\n        this.dialogImageUrl = data.image\n        this.content = data.content\n        this.imgUrl = res.data.data.image\n        this.str = this.content.replace(/(\\s+)?<br(\\s+)?\\/?>(\\s+)?/gi, '')\n        this.fileList2.push({ url: res.data.data.image })\n      }).catch(err => {\n\n      })\n    }\n    this.curId = id\n    this.$axios.post(\"admin/Auth/erAuth\", {\n      token: this.token,\n      id: id\n    }).then(res => {\n      var arr = res.data.data;\n      for (var i = 0; i < arr.length; i++) {\n        if (arr.name == \"上传赠品\") {\n          this.seCurId = arr[i].id;\n        }\n      }\n      this.authList = arr;\n    }).catch(err => {\n\n    })\n  }\n}\n</script>\n<style>\n.Product_value {\n  width: 40%;\n}\n\n.probability {\n  width: 40%;\n}\n\n.activeClass {\n  color: #04bbfc !important;\n}\n\n.avatar-uploader .el-upload {\n  border: 1px dashed #d9d9d9;\n  border-radius: 6px;\n  cursor: pointer;\n  position: relative;\n  overflow: hidden;\n}\n\n.avatar-uploader .el-upload:hover {\n  border-color: #409eff;\n}\n\n.avatar-uploader-icon {\n  font-size: 28px;\n  color: #8c939d;\n  width: 100px;\n  height: 100px;\n  line-height: 100px;\n  text-align: center;\n}\n\n.avatar {\n  width: 100px;\n  height: 100px;\n  display: block;\n}\n\n.ue {\n  height: 20% !important;\n}\n\n.w-e-text-container {\n  height: 120px !important;\n}\n\n.defaultImg {\n  border: 1px dashed #c0ccda;\n  background-color: #fbfdff;\n  width: 148px;\n  height: 148px;\n  line-height: 148px;\n  border-radius: 5px;\n  padding: 10px;\n}\n</style>\n"],"sourceRoot":""}]);

